<template>
  <div class="generator">
    <div class="generator__main-message">
      <p>遅刻のイイワケを考えちゃうゾウ！</p>
    </div>

    <div class="generator__form">
      <span class="generator__subject">{{ subject }}</span>が<span class="generator__reason">{{ reason }}</span>たので遅刻します！！！
      <button @click="create()">イイワケを考える！</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      subject: '',
      reason: ''
    }
  },
  methods: {
    create: function() {
      console.log("考え中...")
      console.log(location.pathname)
      this.axios.get('./json/data.json').then((response) => {
        console.log("うまくいった")
      }).catch((response) => {
        console.log("うまくいかなかった")
      })
    }
  }
}
</script>



